import React from "react"
import { Link } from "gatsby"
import styled from "styled-components"
import { connect } from "react-redux"
import "what-input"

import { rhythm, scale } from "../utils/typography"

const Wrapper = styled.div`
  min-height: 100vh;
`

const PageWrapper = props => {
  const { location, children } = props
  const rootPath = `${__PATH_PREFIX__}/`
  const blogPath = `${__PATH_PREFIX__}/blog/`
  let header

  const BlogHeader = () => (
    <h1
      title="Vitor Alencar Blog"
      style={{ ...scale(1.3), marginBottom: rhythm(1.5), marginTop: 0 }}
    >
      Wellcome to my blog
    </h1>
  )

  const PostHeader = () => (
    <h3 style={{ fontFamily: `Montserrat, sans-serif`, marginTop: 0 }}>
      <Link
        style={{
          boxShadow: `none`,
          textDecoration: `none`,
          color: `inherit`,
          transition: `none`,
        }}
        to={`/blog/`}
      >
        Back
      </Link>
    </h3>
  )

  const RenderHeader = () => {
    const path = location.pathname
    // Avoid display the header on index page
    if (path === rootPath) {
      return null
    }
    if (path === blogPath) {
      return <BlogHeader />
    }

    return <PostHeader />
  }

  return (
    <Wrapper>
      <div
        style={{
          marginLeft: `auto`,
          marginRight: `auto`,
          maxWidth: rhythm(25),
          padding: `${rhythm(1.5)} ${rhythm(3 / 4)}`,
          paddingBottom: 0,
        }}
      >
        <header>{RenderHeader()}</header>
        <main>{children}</main>
      </div>
    </Wrapper>
  )
}

const mapStateToProps = state => ({
  isDarkMode: state.theme.isDarkMode,
})

export default connect(mapStateToProps)(PageWrapper)
